export default {
	data() {
		return {
			cardCur: 0,
			dotStyle: true,
			gridCol: 3,
			loadModal: false,
			//loadProgress: 0
			TabCur: 0,
			tabNav: ['操作员','申请'],
		};
	},
	methods: {
		LoadModal(e) {
			this.loadModal = true;
			setTimeout(() => {
				this.loadModal = false;
			}, 2000)
		},
		
		toChild(e) {
			uni.navigateTo({
				url: e.currentTarget.dataset.url
			})
		},
		
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
	}
}
